.PosDashboard{display:flex;margin:10px 10px 0px 5px}.PosDashboard h2{text-transform:uppercase}.PosDashboard .graphs{margin:0px 10px 10px 10px;width:63%;height:calc(100vh - 70px);display:flex;flex-direction:column}.PosDashboard .graph{width:100%;padding:0px;display:flex;justify-content:space-between;flex:3}.PosDashboard .graph .x_panel{width:49.5%}.PosDashboard .cards-main :nth-child(1){margin-left:0px !important}.PosDashboard .cards-main{display:flex;flex-direction:row;width:100%;flex:1}.PosDashboard .cards-main .cards{width:23%;height:10h;border-radius:5px;padding:.7vw;margin-left:12px;background:#2e303f;box-shadow:0 2px 5px 0 rgba(0,0,0,.2),0 4px 10px 0 rgba(0,0,0,.19)}.PosDashboard .cards-main .cards .card-top{display:flex;justify-content:space-between;align-items:center;margin-bottom:5px}.PosDashboard .cards-main .cards .card-top .count_top{font-size:1vw}.PosDashboard .cards-main .cards .card-top .count{font-size:1.3vw;font-weight:600;color:#ff7139}.PosDashboard .cards-main .cards .count_bottom{display:flex;justify-content:space-between}.PosDashboard .cards-main .cards .count_bottom #revenue_YTD_per{font-size:.8vw;font-style:inherit}.PosDashboard .tables{width:27%}.PosDashboard .tables .x_panel{display:flex;flex-direction:column;padding:0px;width:95%;margin-left:10px;height:calc(100vh - 70px)}.PosDashboard .tables .x_panel .table-flex{flex:1;padding:8px}.PosDashboard .tables .x_content{margin-top:0px}.PosDashboard .label{margin-bottom:0px;padding:3px;font-size:12px;font-weight:unset}.PosDashboard .select{color:#fff;background-color:rgba(0,0,0,0);padding-top:6px;padding-bottom:6px;border:none;font-size:12px}.PosDashboard .select option{background-color:#2e303f}.PosDashboard .header-dropdown{color:#1ee9d1 !important;font-size:1vw;padding-left:0px}.PosDashboard p{color:red;font-size:12px}.PosDashboard img{width:18px}.PosDashboard .btn-primary{width:150px}.PosDashboard .bar{fill:#0ce0d4;opacity:.7}.PosDashboard .bar:hover{opacity:.4}.PosDashboard .axis{font:10px sans-serif}.PosDashboard .axis path{fill:none;stroke:#adb2b5;shape-rendering:crispEdges}.PosDashboard .y line{fill:none;stroke:#adb2b5;shape-rendering:crispEdges}.PosDashboard .x line{fill:none;stroke:none;shape-rendering:crispEdges}.PosDashboard .d3-tip{line-height:1;font-weight:bold;padding:12px;background:#2a3f54;color:#fff;border-radius:2px}.PosDashboard .d3-tip:after{box-sizing:border-box;display:inline;font-size:10px;width:100%;line-height:1;color:rgba(0,0,0,.8);content:"▼";position:absolute;text-align:center}.PosDashboard .d3-tip.n:after{margin:-1px 0 0 0;top:100%;left:0}.PosDashboard text{fill:#f5f5f5;font-size:11.5px}.PosDashboard .revenuetooltip{padding:0px !important}.PosDashboard .tooltip{padding:0px !important}.PosDashboard .padding{padding:15px !important;color:#fff !important}.PosDashboard .country-name{margin:0 !important}.PosDashboard .modal-content{background-color:#fff !important;color:rgba(42,63,84,.9098039216)}.PosDashboard .modal-header .close{color:rgba(42,63,84,.9098039216);opacity:1}@media(max-width: 991px){.PosDashboard{flex-direction:column}.PosDashboard .graphs{width:100%;margin-left:0px}.PosDashboard .graphs .graph{width:100% !important}.PosDashboard .tables{width:100%;margin-left:0px}}@media(max-height: 620px){.PosDashboard .reduce-margin{margin-bottom:0px}}